/* src/kavumainpage/Spinner.css */
.spinner-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #0c1a3e; /* A dark blue matching a night sky */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.spinner-container {
  width: 60px;
  height: 60px;
  border: 5px solid rgba(255, 255, 255, 0.2);
  border-top-color: #f7b733; /* A golden color */
  border-radius: 50%;
  animation: spin 1s ease-in-out infinite;
}

.spinner-text {
  margin-top: 20px;
  color: #f7b733;
  font-size: 1.2em;
  font-family: Arial, sans-serif;
  text-shadow: 0 0 5px rgba(247, 183, 51, 0.7);
}


@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}