        .special-committee .member-name { font-size: 1.4rem; }
        .special-committee .member-designation { font-size: 1.1rem; }
        .horizontal-scroll-container { width: 100%; overflow: hidden; position: relative; cursor: grab; user-select: none; padding: 10px 0; }
