        /* Modal Styles */
 .modal-overlay { 
    position: fixed; 
    top: 0; 
    left: 0; 
    width: 100%; 
    height: 100%; 
    background-color: var(--modal-overlay-bg); 
    display: flex; 
    justify-content: center; 
    align-items: center; 
    z-index: 1000; 
    opacity: 0; 
    visibility: hidden; 
    transition: opacity 0.3s ease, visibility 0.3s ease; 
}

.modal-overlay.active { 
    opacity: 1; 
    visibility: visible; 
}

/* This is the main content box of the modal.
   We use flexbox here to control the layout of its children (title, paragraph). */
.modal-content { 
    background-color: var(--modal-paper-bg); 
    border: 5px double var(--modal-border-color); 
    border-radius: 8px; 
    padding: 30px; 
    max-width: 80%; 
    width: 90%; 
    max-height: 90vh; 
    overflow-y: auto; 
    position: relative; 
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5); 
    transform: scale(0.9); 
    transition: transform 0.3s ease; 
    color: var(--modal-text-color); 
    font-family: 'Old Standard TT', serif; 
    line-height: 1.8; 
    font-size: 1.15rem;
    
    /* --- ADD THESE THREE LINES --- */
    display: flex;              /* Enables flexbox layout */
    flex-direction: column;     /* Stacks children vertically (title above paragraph) */
    align-items: center;        /* Centers the children horizontally */
}

.modal-overlay.active .modal-content { 
    transform: scale(1); 
}

/* Style for the modal title (h3) */
.modal-content h3 { 
    font-family: 'Manjari', 'Noto Sans Malayalam', sans-serif; 
    color: var(--modal-border-color); 
    font-size: 2.2rem; 
    margin-bottom: 25px; 
    text-align: center; 
    width: 100%; /* Ensures the text-align works correctly within the flex container */
}

/* Style for the modal paragraph (p) */
.modal-content p { 
    margin-bottom: 15px;
    width: 100%; /* Ensures the paragraph takes up the full width */
    text-align: justify; /* Makes the paragraph text more readable */
}

/* Style for images inside the modal (for committee members) */
.modal-content img { 
    max-width: 100%; 
    height: auto; 
    display: block; 
    margin: 20px auto; 
    border-radius: 8px; 
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3); 
}

/* Style for committee member info */
.modal-content .member-info { 
    text-align: center; 
    font-size: 1.2rem; 
    font-weight: bold; 
    margin-top: 10px; 
    color: var(--dark-title-color); 
}

/* Style for the close button */
.modal-close-btn { 
    position: absolute; 
    top: 15px; 
    right: 20px; 
    background: none; 
    border: none; 
    font-size: 2rem; 
    color: var(--modal-border-color); 
    cursor: pointer; 
    transition: transform 0.2s ease; 
}

.modal-close-btn:hover { 
    transform: rotate(90deg); 
}

        /* Fullscreen Image Viewer Styles */
        .image-viewer-overlay {
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba(0,0,0,0.9);
            z-index: 2000;
            display: flex;
            justify-content: center;
            align-items: center;
            opacity: 0;
            visibility: hidden;
            transition: opacity 0.3s ease, visibility 0.3s ease;
        }
        .image-viewer-overlay.active {
            opacity: 1;
            visibility: visible;
        }
        .image-viewer-content {
            max-width: 90vw;
            max-height: 90vh;
            width: auto;
            height: auto;
            object-fit: contain;
            transform: scale(0.8);
            transition: transform 0.3s ease;
        }
        .image-viewer-overlay.active .image-viewer-content {
            transform: scale(1);
        }
       .image-viewer-close {
  position: absolute;
