.spinner-overlay {
    position: fixed; /* Fixes it to the viewport */
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6); /* Semi-transparent dark overlay */
    display: flex;
    justify-content: center; /* Center horizontally */
    align-items: center;   /* Center vertically */
    z-index: 9999; /* Ensure it's on top of everything */
    opacity: 0;
    transition: opacity 0.3s ease-in-out; /* Smooth fade effect */
    pointer-events: none; /* Allows clicks through when hidden */
}

.spinner-overlay.visible {
    opacity: 1;
    pointer-events: auto; /* Disables clicks when visible */
}

.spinner-overlay.hidden {
    opacity: 0;
    pointer-events: none;
}

/* Spinner container for centering the spinner itself */
.spinner-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%; /* Take full width of parent (spinner-overlay) */
    height: 100%; /* Take full height of parent (spinner-overlay) */
}

/* The actual spinning animation */
.spinner {
    width: 48px; /* Default size, can be overridden by Tailwind classes */
    height: 48px; /* Default size, can be overridden by Tailwind classes */
    border-radius: 50%;
    display: inline-block;
    border-top: 3px solid #FFF; /* Default color, can be overridden by Tailwind classes */
    border-right: 3px solid transparent;
    box-sizing: border-box;
    animation: rotation 1s linear infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}